/* global.css */
p {
  font-size: var(--uitk-text-font-size);
}
body {
  font-family: "uitk-sans";
}
uitk-theme {
  display: contents;
}

/* css/OpenSans.css */
@font-face {
  font-family: "uitk-sans";
  font-style: normal;
  font-weight: 200;
  src: url(./css/fonts/OpenSans-Light.ttf) format("truetype");
}
@font-face {
  font-family: "uitk-sans";
  font-style: italic;
  font-weight: 200;
  src: url(./css/fonts/OpenSans-LightItalic.ttf) format("truetype");
}
@font-face {
  font-family: "uitk-sans";
  font-style: normal;
  font-weight: 400;
  src: url(./css/fonts/OpenSans-Regular.ttf) format("truetype");
}
