  font-family: "uitk-sans";
  font-style: italic;
  font-weight: 400;
  src: url(./css/fonts/OpenSans-Italic.ttf) format("truetype");
}
@font-face {
  font-family: "uitk-sans";
  font-style: normal;
  font-weight: 600;
  src: url(./css/fonts/OpenSans-Semibold.ttf) format("truetype");
}
@font-face {
  font-family: "uitk-sans";
  font-style: italic;
  font-weight: 600;
  src: url(./css/fonts/OpenSans-SemiboldItalic.ttf) format("truetype");
}
@font-face {
  font-family: "uitk-sans";
  font-style: normal;
  font-weight: 700;
  src: url(./css/fonts/OpenSans-Bold.ttf) format("truetype");
}
@font-face {
  font-family: "uitk-sans";
  font-style: italic;
  font-weight: 700;
  src: url(./css/fonts/OpenSans-BoldItalic.ttf) format("truetype");
}
@font-face {
  font-family: "uitk-sans";
  font-style: normal;
  font-weight: 800;
  src: url(./css/fonts/OpenSans-ExtraBold.ttf) format("truetype");
}
@font-face {
  font-family: "uitk-sans";
  font-style: italic;
  font-weight: 800;
  src: url(./css/fonts/OpenSans-ExtraBoldItalic.ttf) format("truetype");
}

