.uitk-dark {
  --uitk-color-grey-20-fade-40: rgba(234, 237, 239, 0.4);
  --uitk-color-grey-600-fade-40: rgba(47, 49, 54, 0.4);
  --uitk-color-grey-60-fade-40: rgba(197, 201, 208, 0.4);
  --uitk-color-grey-40-fade-40: rgba(217, 221, 227, 0.4);
  --uitk-color-grey-400-fade-40: rgba(68, 72, 79, 0.4);
  --uitk-color-grey-500-fade-40: rgba(59, 63, 70, 0.4);
  --uitk-color-grey-90-fade-20: rgba(132, 135, 142, 0.2);
  --uitk-color-grey-100-fade-20: rgba(116, 119, 127, 0.2);
}
.uitk-light {
  --uitk-color-blue-30-fade-99: rgba(164, 213, 244, 0.99);
}
.uitk-dark {
  --uitk-color-blue-700-fade-99: rgba(0, 71, 123, 0.99);
}

/* css/foundations/icon.css */
.uitk-light {
  --uitk-icon-primary-color: var(--uitk-color-grey-200);
  --uitk-icon-primary-color-disabled: rgba(97, 101, 110, var(--uitk-disabled-graphical-opacity));
}
.uitk-dark {
  --uitk-icon-primary-color: var(--uitk-color-grey-60);
  --uitk-icon-primary-color-disabled: rgba(197, 201, 208, var(--uitk-disabled-graphical-opacity));
}

/* css/foundations/shadow.css */
.uitk-light {
  --uitk-shadow-0: none;
  --uitk-shadow-1: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  --uitk-shadow-2: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  --uitk-shadow-3: 0 4px 8px 0 rgba(0, 0, 0, 0.15);
  --uitk-shadow-4: 0 6px 10px 0 rgba(0, 0, 0, 0.2);
  --uitk-shadow-5: 0 12px 40px 5px rgba(0, 0, 0, 0.3);
}
.uitk-dark {
  --uitk-shadow-0: none;
  --uitk-shadow-1: 0 1px 3px 0 rgba(0, 0, 0, 0.5);
  --uitk-shadow-2: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  --uitk-shadow-3: 0 4px 8px 0 rgba(0, 0, 0, 0.55);
  --uitk-shadow-4: 0 6px 10px 0 rgba(0, 0, 0, 0.55);
  --uitk-shadow-5: 0 12px 40px 5px rgba(0, 0, 0, 0.65);
}

/* css/foundations/size.css */
.uitk-density-touch,
.uitk-density-low,
.uitk-density-medium,
.uitk-density-high {
  --uitk-size-basis-unit: 4px;
  --uitk-size-icon-small: 12px;
  --uitk-size-icon-medium: 24px;
  --uitk-size-icon-large: 48px;
  --uitk-size-regular-high: 20px;
  --uitk-size-regular-medium: 28px;
  --uitk-size-regular-low: 36px;
  --uitk-size-regular-touch: 44px;
  --uitk-size-stackable-high: 24px;
  --uitk-size-stackable-medium: 36px;
  --uitk-size-stackable-low: 48px;
  --uitk-size-stackable-touch: 60px;
  --uitk-size-toolbar-high: 28px;
  --uitk-size-toolbar-medium: 44px;
  --uitk-size-toolbar-low: 60px;
  --uitk-size-toolbar-touch: 76px;
}
.uitk-density-touch {
  --uitk-size-regular-unit: var(--uitk-size-regular-touch);
  --uitk-size-stackable-unit: var(--uitk-size-stackable-touch);
  --uitk-size-toolbar-unit: var(--uitk-size-toolbar-touch);
}
.uitk-density-low {
  --uitk-size-regular-unit: var(--uitk-size-regular-low);
  --uitk-size-stackable-unit: var(--uitk-size-stackable-low);
  --uitk-size-toolbar-unit: var(--uitk-size-toolbar-low);
}
.uitk-density-medium {
  --uitk-size-regular-unit: var(--uitk-size-regular-medium);
  --uitk-size-stackable-unit: var(--uitk-size-stackable-medium);
  --uitk-size-toolbar-unit: var(--uitk-size-toolbar-medium);
}
.uitk-density-high {
  --uitk-size-regular-unit: var(--uitk-size-regular-high);
  --uitk-size-stackable-unit: var(--uitk-size-stackable-high);
  --uitk-size-toolbar-unit: var(--uitk-size-toolbar-unit);
}

/* css/foundations/spacing.css */
.uitk-density-touch,
.uitk-density-low,
.uitk-density-medium,
.uitk-density-high {
  --uitk-spacing-touch: 16px;
  --uitk-spacing-low: 12px;
  --uitk-spacing-medium: 8px;
  --uitk-spacing-high: 4px;
}
.uitk-density-touch {
  --uitk-spacing-unit: var(--uitk-spacing-touch);
}
.uitk-density-low {
  --uitk-spacing-unit: var(--uitk-spacing-low);
}
.uitk-density-medium {
  --uitk-spacing-unit: var(--uitk-spacing-medium);
}
.uitk-density-high {
  --uitk-spacing-unit: var(--uitk-spacing-high);
}

/* css/foundations/typography.css */
.uitk-density-touch,
.uitk-density-low,
.uitk-density-medium,
.uitk-density-high {
  --uitk-typography-font-family: uitk-sans;
  --uitk-typography-weight-light: 300;
  --uitk-typography-weight-regular: 400;
  --uitk-typography-weight-medium: 500;
  --uitk-typography-weight-semiBold: 600;
  --uitk-typography-weight-bold: 700;
  --uitk-typography-weight-extraBold: 800;
  --uitk-typography-line-height: 1.3;
  --uitk-typography-size-10: 8px;
  --uitk-typography-size-30: 10px;
  --uitk-typography-size-40: 11px;
  --uitk-typography-size-50: 12px;
