  --uitk-typography-size-180: 44px;
  --uitk-typography-size-210: 56px;
  --uitk-typography-size-240: 64px;
}
.uitk-light,
.uitk-dark {
  font-family: "uitk-sans";
}
.uitk-light,
.uitk-dark {
  font-family: var(--uitk-typography-font-family);
  font-size: 14px;
}

/* css/foundations/zindex.css */
.uitk-density-touch,
.uitk-density-low,
.uitk-density-medium,
.uitk-density-high {
  --uitk-zindex-default: 1;
  --uitk-zindex-popout: 1000;
  --uitk-zindex-docked: 1050;
  --uitk-zindex-appheader: 1100;
  --uitk-zindex-drawer: 1200;
  --uitk-zindex-modal: 1300;
  --uitk-zindex-notification: 1400;
  --uitk-zindex-dragobject: 1420;
  --uitk-zindex-contextmenu: 1450;
  --uitk-zindex-tooltip: 1500;
}

/* css/characteristics/accent.css */
.uitk-light {
  --uitk-accent-border-color: var(--uitk-color-blue-300);
  --uitk-accent-background: var(--uitk-color-blue-500);
  --uitk-accent-text-color: var(--uitk-color-white);
}
.uitk-dark {
  --uitk-accent-border-color: var(--uitk-color-blue-300);
  --uitk-accent-background: var(--uitk-color-blue-500);
